<template id="login">
<transition name="modal">
<div class="modal-mask">
    <div class="modal-wrapper">
<div class="modal-container">
  <form v-on:submit.prevent="submission()">
      <div class="modal-header">
      <p>Login</p>
      </div>
      <div class="from-group modal-boday">
      <i class="fa fa-user"></i><input v-model="info.user" type="text" class="form-control" placeholder="Login"/>
      </div>
      <div class="form-group modal-body">
      <i class="fa fa-lock"></i><input v-model="info.password" type="password" class="form-control" placeholder="Password"/>
      </div>
      <button type="submit" class="btn btn-primary">Login</button>
      
  </form>
</div>
</div>
</div>
</transition>
</template>

<script>
import userService from '../../middleware/user-mid'

export default {
  name: "login",
  data() {
    return {
      info: {
        user: '',
        password: ''
      }
    };
  },
  methods: {
      submission: function() {
          console.log(this.info);
          var token = userService.auth(this.info);
          var user = token.then(value => userService.verifyToken(value));
          this.$emit('close', user);
      }
  }
};
</script>

<style>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}

.modal-body {
  margin: 20px 0;
}

.modal-default-button {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>



